import React, { useContext } from "react";
import { Field } from "react-final-form";
import { useLocation, useNavigate } from "react-router-dom";
import { InputField, Condition, Error, Wizard } from "common/components";
import { labels, questions, required } from "common";
import AppContext from "store/app-store";

function ServicesSurvey() {
  const { getLang } = useContext(AppContext);
  const navigate = useNavigate();
  const location = useLocation();
  const sleep = (ms) => new Promise((resolve) => setTimeout(resolve, ms));

  const onSubmit = async (values) => {
    await sleep(300);
    // window.alert(JSON.stringify(values, 0, 2));
    navigate("/" + getLang() + "/services-survey-summary", {
      state: { values },
    });
  };

  return (
    <>
      <Wizard
        initialValues={
          location?.state
            ? location?.state
            : // ? { ...location?.state, page: 4 }
              {
                isFlightSelected: "Yes",
                isAccommodationSelected: "Yes",
                isTransportSelected: "Yes",
                isTranslationSelected: "Yes",
                isTouristSelected: "Yes",
              }
        }
        onSubmit={onSubmit}
      >
        {/* Question 1: Flight - START */}
        <Wizard.Page
          serviceTitle={questions[0].title}
          serviceBanner={questions[0].banner}
          validate={(values) => {
            const errors = {};
            if (!values.isFlightSelected) {
              errors.isFlightSelected = required;
            } else if (values.isFlightSelected === "Yes") {
              if (!values.flight_AdultTravellers)
                errors.flight_AdultTravellers = required;
              if (!values.flight_InfantTravellers)
                errors.flight_InfantTravellers = required;
              if (!values.flight_Class) errors.flight_Class = required;
            }
            return errors;
          }}
        >
          <div className="form-group row">
            <label className="col-form-label col-md-4">
              {questions[0].text}
              <div className="invalid-feedback d-block"></div>
            </label>
            <div className="col-md-7">
              <div className="radio">
                <label htmlFor="yes_radio">
                  <Field
                    id="yes_radio"
                    className="radio"
                    name={questions[0].selectionCondition}
                    value={"Yes"}
                    type="radio"
                    component={InputField}
                    label={"Yes"}
                    showerrorbeside="true"
                  />
                </label>
              </div>
              <div className="radio">
                <label htmlFor="no_radio">
                  <Field
                    id="no_radio"
                    className="radio"
                    name={questions[0].selectionCondition}
                    value={"No"}
                    type="radio"
                    component={InputField}
                    label={"No (Skip to the next service)"}
                    hideerror="true"
                  />
                </label>
              </div>
            </div>
          </div>

          <Condition when={questions[0].selectionCondition} is="Yes">
            <div className="mt-3">
              <div className="form-group row">
                <label className="col-form-label col-md-4">
                  {labels[questions[0].fields[0].name]}
                </label>
                <div className="col-md-7 sameline-block">
                  <Field
                    name={questions[0].fields[0].name}
                    className="form-control numeric-input"
                    type="text"
                    component={InputField}
                    showerrorbeside="true"
                  />
                </div>
              </div>

              <div className="form-group row">
                <label className="col-form-label col-md-4">
                  {labels[questions[0].fields[1].name]}
                </label>
                <div className="col-md-7 sameline-block">
                  <Field
                    name={questions[0].fields[1].name}
                    className="form-control numeric-input"
                    type="text"
                    component={InputField}
                    showerrorbeside="true"
                  />
                </div>
              </div>

              <div className="form-group row">
                <label className="col-form-label col-md-4">
                  {labels[questions[0].fields[2].name]}
                </label>
                <div className="col-md-3 sameline-block">
                  <Field
                    name={questions[0].fields[2].name}
                    className="form-control"
                    component="select"
                    showerrorbeside="true"
                  >
                    <option />
                    {["First Class", "Economy", "I'll decide Later"].map(
                      (val, index) => (
                        <option key={index} value={val}>
                          {val}
                        </option>
                      )
                    )}
                  </Field>
                  <Error name={"flight_Class"} showerrorbeside="true" />
                </div>
              </div>

              <div className="form-group row">
                <label className="col-form-label col-md-4">
                  {labels[questions[0].fields[3].name]}
                </label>
                <div className="col-md-8 sameline-block">
                  <Field
                    name={questions[0].fields[3].name}
                    className="form-control"
                    type="textarea"
                    component={InputField}
                    placeholder={"(ex. non stop, isle/window, halal food …etc)"}
                    showerrorbeside="true"
                  />
                </div>
              </div>
            </div>
          </Condition>
        </Wizard.Page>
        {/* Question 1: Flight - END */}

        {/* Question 2: Accomodation - START */}
        <Wizard.Page
          serviceTitle={questions[1].title}
          serviceBanner={questions[1].banner}
          validate={(values) => {
            const errors = {};
            if (!values.isAccommodationSelected) {
              errors.isAccommodationSelected = required;
            } else if (values.isAccommodationSelected === "Yes") {
              if (!values.accomodation_Proximity)
                errors.accomodation_Proximity = required;
              if (!values.accomodation_Stars)
                errors.accomodation_Stars = required;
              if (!values.accomodation_Guests)
                errors.accomodation_Guests = required;
              if (!values.accomodation_Rooms)
                errors.accomodation_Rooms = required;
              if (!values.accomodation_Beds)
                errors.accomodation_Beds = required;
            }
            return errors;
          }}
        >
          <div className="form-group row">
            <label className="col-form-label col-md-4">
              {questions[1].text}
              <div className="invalid-feedback d-block"></div>
            </label>
            <div className="col-md-7">
              <div className="radio">
                <label htmlFor="yes_radio">
                  <Field
                    id="yes_radio"
                    className="radio"
                    name={questions[1].selectionCondition}
                    value={"Yes"}
                    type="radio"
                    component={InputField}
                    label={"Yes"}
                    showerrorbeside="true"
                  />
                </label>
              </div>
              <div className="radio">
                <label htmlFor="no_radio">
                  <Field
                    id="no_radio"
                    className="radio"
                    name={questions[1].selectionCondition}
                    value={"No"}
                    type="radio"
                    component={InputField}
                    label={"No (Skip to the next service)"}
                    hideerror="true"
                  />
                </label>
              </div>
            </div>
          </div>
          <Condition when={questions[1].selectionCondition} is="Yes">
            <div className="mt-3">
              <div className="form-group row">
                <label className="col-form-label col-md-4">
                  {labels[questions[1].fields[0].name]}
                </label>
                <div className="col-md-7 sameline-block">
                  <label>
                    <Field
                      name={questions[1].fields[0].name}
                      value={"Close"}
                      type="radio"
                      component={InputField}
                      label={"Close"}
                      showerrorbeside="true"
                      hideerror="true"
                    />
                  </label>
                  <label className="ml-4">
                    <Field
                      name={questions[1].fields[0].name}
                      value={"Does not matter"}
                      type="radio"
                      component={InputField}
                      label={"Does not matter"}
                      showerrorbeside="true"
                    />
                  </label>
                </div>
              </div>

              <div className="form-group row">
                <label className="col-form-label col-md-4">
                  {labels[questions[1].fields[1].name]}
                </label>
                <div className="col-md-4 sameline-block">
                  <Field
                    name={questions[1].fields[1].name}
                    className="form-control"
                    component="select"
                    showerrorbeside="true"
                  >
                    <option />
                    {["5 Stars", "4 Stars", "3 Stars", "Does not matter"].map(
                      (val, index) => (
                        <option key={index} value={val}>
                          {val}
                        </option>
                      )
                    )}
                  </Field>
                  <Error name={"accomodation_Stars"} showerrorbeside="true" />
                </div>
              </div>

              <div className="form-group row">
                <label className="col-form-label col-md-4">
                  {labels[questions[1].fields[2].name]}
                </label>
                <div className="col-md-7 sameline-block">
                  <Field
                    name={questions[1].fields[2].name}
                    className="form-control numeric-input"
                    type="text"
                    component={InputField}
                    showerrorbeside="true"
                  />
                </div>
              </div>

              <div className="form-group row">
                <label className="col-form-label col-md-4">
                  {labels[questions[1].fields[3].name]}
                </label>
                <div className="col-md-7 sameline-block">
                  <Field
                    name={questions[1].fields[3].name}
                    className="form-control numeric-input"
                    type="text"
                    component={InputField}
                    showerrorbeside="true"
                  />
                </div>
              </div>

              <div className="form-group row">
                <label className="col-form-label col-md-4">
                  {labels[questions[1].fields[4].name]}
                </label>
                <div className="col-md-7 sameline-block">
                  <Field
                    name={questions[1].fields[4].name}
                    className="form-control numeric-input"
                    type="text"
                    component={InputField}
                    showerrorbeside="true"
                  />
                </div>
              </div>

              <div className="form-group row">
                <label className="col-form-label col-md-4">
                  {labels[questions[1].fields[5].name]}
                </label>
                <div className="col-md-8 sameline-block">
                  <Field
                    name={questions[1].fields[5].name}
                    className="form-control"
                    type="textarea"
                    component={InputField}
                    placeholder={"(ex. seaview, non-smoking …etc)"}
                    showerrorbeside="true"
                  />
                </div>
              </div>
            </div>
          </Condition>
        </Wizard.Page>
        {/* Question 2: Accomodation - END */}

        {/* Question 3: Transport - START */}
        <Wizard.Page
          serviceTitle={questions[2].title}
          serviceBanner={questions[2].banner}
          validate={(values) => {
            const errors = {};
            if (!values.isTransportSelected) {
              errors.isTransportSelected = required;
            } else if (values.isTransportSelected === "Yes") {
              if (!values.transport_CarClass)
                errors.transport_CarClass = required;
              if (!values.transport_Biscuit)
                errors.transport_Biscuit = required;
            }
            return errors;
          }}
        >
          <div className="form-group row">
            <label className="col-form-label col-md-4">
              {questions[2].text}
              <div className="invalid-feedback d-block"></div>
            </label>
            <div className="col-md-7">
              <div className="radio">
                <label htmlFor="yes_radio">
                  <Field
                    id="yes_radio"
                    className="radio"
                    name={questions[2].selectionCondition}
                    value={"Yes"}
                    type="radio"
                    component={InputField}
                    label={"Yes"}
                    showerrorbeside="true"
                  />
                </label>
              </div>
              <div className="radio">
                <label htmlFor="no_radio">
                  <Field
                    id="no_radio"
                    className="radio"
                    name={questions[2].selectionCondition}
                    value={"No"}
                    type="radio"
                    component={InputField}
                    label={"No (Skip to the next service)"}
                    hideerror="true"
                  />
                </label>
              </div>
            </div>
          </div>
          <Condition when={questions[2].selectionCondition} is="Yes">
            <div>
              <div className="form-group row">
                <label className="col-form-label col-md-4">
                  {labels[questions[2].fields[0].name]}
                </label>
                <div className="col-md-4 sameline-block">
                  <Field
                    name={questions[2].fields[0].name}
                    className="form-control"
                    component="select"
                    showerrorbeside="true"
                  >
                    <option />
                    {["Economy Car", "Luxury Car", "With Driver"].map(
                      (val, index) => (
                        <option key={index} value={val}>
                          {val}
                        </option>
                      )
                    )}
                  </Field>
                  <Error name={"transport_CarClass"} showerrorbeside="true" />
                </div>
              </div>

              <div className="form-group row">
                <label className="col-form-label col-md-4">
                  {labels[questions[2].fields[1].name]}
                </label>
                <div className="col-md-7 sameline-block">
                  <label>
                    <Field
                      name={questions[2].fields[1].name}
                      value={"Yes"}
                      type="radio"
                      component={InputField}
                      label={"Yes"}
                      showerrorbeside="true"
                      hideerror="true"
                    />
                  </label>
                  <label className="ml-4">
                    <Field
                      name={questions[2].fields[1].name}
                      value={"No"}
                      type="radio"
                      component={InputField}
                      label={"No"}
                      showerrorbeside="true"
                    />
                  </label>
                </div>
              </div>
            </div>
          </Condition>
        </Wizard.Page>
        {/* Question 3: Transport - END */}

        {/* Question 4: Translation - START */}
        <Wizard.Page
          serviceTitle={questions[3].title}
          serviceBanner={questions[3].banner}
          validate={(values) => {
            const errors = {};
            if (!values.isTranslationSelected) {
              errors.isTranslationSelected = required;
            } else if (values.isTranslationSelected === "Yes") {
              if (!!!values.translation_LangFrom)
                errors.translation_LangFrom = required;
            }
            return errors;
          }}
        >
          <div className="form-group row">
            <label className="col-form-label col-md-4">
              {questions[3].text}
              <div className="invalid-feedback d-block"></div>
            </label>
            <div className="col-md-7">
              <div className="radio">
                <label htmlFor="yes_radio">
                  <Field
                    id="yes_radio"
                    className="radio"
                    name={questions[3].selectionCondition}
                    value={"Yes"}
                    type="radio"
                    component={InputField}
                    label={"Yes"}
                    showerrorbeside="true"
                  />
                </label>
              </div>
              <div className="radio">
                <label htmlFor="no_radio">
                  <Field
                    id="no_radio"
                    className="radio"
                    name={questions[3].selectionCondition}
                    value={"No"}
                    type="radio"
                    component={InputField}
                    label={"No (Skip to the next service)"}
                    hideerror="true"
                  />
                </label>
              </div>
            </div>
          </div>
          <Condition when={questions[3].selectionCondition} is="Yes">
            <div>
              <div className="form-group row">
                <label className="col-form-label col-md-4">
                  {labels[questions[3].fields[0].name]}
                </label>
                <div className="col-md-4 sameline-block">
                  <Field
                    name={questions[3].fields[0].name}
                    className="form-control"
                    component="select"
                    showerrorbeside="true"
                  >
                    <option />
                    {["Arabic العربية", "English", "Russian Русский"].map(
                      (val, index) => (
                        <option key={index} value={val}>
                          {val}
                        </option>
                      )
                    )}
                  </Field>
                  <Error name={"translation_LangFrom"} showerrorbeside="true" />
                </div>
              </div>
            </div>
          </Condition>
        </Wizard.Page>
        {/* Question 4: Translation - END */}

        {/* Question 5: Tourist - START */}
        <Wizard.Page
          serviceTitle={questions[4].title}
          serviceBanner={questions[4].banner}
          validate={(values) => {
            const errors = {};
            if (!values.isTouristSelected) {
              errors.isTouristSelected = required;
            } else if (values.isTouristSelected === "Yes") {
              // if (!values.accomodation_Beds) errors.accomodation_Beds = required;
            }
            return errors;
          }}
        >
          <div className="form-group row">
            <label className="col-form-label col-md-4">
              {questions[4].text}
              <div className="invalid-feedback d-block"></div>
            </label>
            <div className="col-md-7">
              <div className="radio">
                <label htmlFor="yes_radio">
                  <Field
                    id="yes_radio"
                    className="radio"
                    name={questions[4].selectionCondition}
                    value={"Yes"}
                    type="radio"
                    component={InputField}
                    label={"Yes"}
                    showerrorbeside="true"
                  />
                </label>
              </div>
              <div className="radio">
                <label htmlFor="no_radio">
                  <Field
                    id="no_radio"
                    className="radio"
                    name={questions[4].selectionCondition}
                    value={"No"}
                    type="radio"
                    component={InputField}
                    label={"No (Skip to the next service)"}
                    hideerror="true"
                  />
                </label>
              </div>
            </div>
          </div>
          <Condition when={questions[4].selectionCondition} is="Yes">
            <div>
              <label>
                choose from predefined drop list (with pictures and info)
              </label>
            </div>
          </Condition>
        </Wizard.Page>
        {/* Question 5: Tourist - END */}

        {/* <ToggleSample />
        <Sample serviceBanner={servive1Banner} /> */}
      </Wizard>
    </>
  );
}

export default ServicesSurvey;
